<!--血糖入口-->
<template>
  <!--头部-->
  <div class="HeartRate">
    <s-header :name="'心率'"  :rightName="'历史数据'" :rightPress="rightPress"/>
    <!--选择设备-->
    <change-device from-page="heartRate" :backgroundColor="'#06B48D'" :otherAddClick="otherAddClick"/>
    <div class="div_top">
      <span class="measure">最近一次测量</span>
      <span class="date">{{ detial.measureTimeDesc || "" }}</span>
      <!--圆环显示-->
      <div class="circular">
        <DefineCircleProgress :num = "detial.heartRate || '--'" />
      </div>
      <!--血糖状态-->
      <track-type :datas="['心率过缓','正常','心率过速']" :type="1" :style="{marginTop:'10px'}"/>
      <!--      <div class="div_des">-->
      <!--        随机/未用药-->
      <!--      </div>-->
      <div class="list-row-item">
        <div class="mixed">
          <p>{{ detial.maxHeartRate || "--" }}</p>
          <p>峰值</p>
        </div>
        <div class="mixed">
          <p>{{ detial.minHeartRate || "--" }}</p>
          <p>谷值</p>
        </div>
        <div class="mixed">
          <p>{{ detial.avgHeartRate || "--" }}</p>
          <p>均值</p>
        </div>
      </div>
    </div>
    <div class="card">
      <p class="title">
        数据解读
      </p>
      <p class="desc">
        {{ detial.typeStr || "暂无数据解读" }}
      </p>
    </div>
  </div>
</template>

<script>
import sHeader from '@/components/SimpleHeader'
import {reactive, onMounted, toRefs, ref} from 'vue'
import ChangeDevice from '../components/ChangeDevice'
import TrackType from '../components/TrackType'
import TrackData from '../TrackData'
import {useRouter} from "vue-router";
import {Toast} from "vant";
import {getHeartRateDetail} from "@/service/heartrate";
import {formatYYMMDD} from "@/utils/util";
import DefineCircleProgress from "@/components/DefineCircleProgress";
const {computed} = require("vue");

export default {
  name: "index",
  components: {
    DefineCircleProgress,
    sHeader,
    ChangeDevice,
    TrackType,
    TrackData,
  },
  setup() {

    onMounted(
        () => {
          requestHeartRateDetail()
        }
    )

    const router = useRouter();
    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      todo: '',
      selectDate: new Date(),
      detial: {},
    })



    const goTo = (r, query) => {
      router.push({path: r, query: query || {}})
    }

    const requestHeartRateDetail = async () => {
        const res = await getHeartRateDetail(formatYYMMDD(state.selectDate))
        const { code, data = {} } = res
        if(code === 200){
          state.detial = data
        }
    }
    const onTypeClick = (index) => {
      alert(index)
    }
    const onchange = (data) => {
      state.selectdate = data
    }
    const rightPress = () => {
      goTo('/history-heart-rate')
    }

    // 添加数据
    const otherAddClick = () => {
      Toast("添加数据")
      goTo('/heartrate-add-record')
    }
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      onTypeClick,
      onchange,
      rightPress,
      otherAddClick
    }
  }
}
</script>

